@import "../../assets/styles/variables";

.lang {
  color: $mainAccent;
  width: 48px;
  height: 48px;
  background: none;
  outline: none;
  border: 1px solid transparent;
  border-radius: 50%;
  box-sizing: border-box;
  transition: all $short ease;

  &:hover {
    color: $lightGrey;
    border: 1px solid $lightGrey; }

  &:active {
    color: $mainAccent; } }

.inMenu {
  display: none; }

@media screen and ( max-width:  $tablet) {

  .inMenu {
    height: 16px;
    width: 31px;
    margin-top: auto;
    padding: 0;
    display: block;
    border-radius: 0;
    &:hover {
      border: none; }
    &:active {
      color: $lightGrey; } } }
