@import "../../assets/styles/variables";

.header {
  display: flex; }

.wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between; }

.logo {
  font-size: 30px;
  line-height: 35px;
  font-weight: bold;
  text-decoration: none;
  color: $mainAccent; }

.location {
  display: flex;
  align-items: center; }

.city {
  font-size: 14px;
  line-height: 16px;
  color: $darkGrey; }

.icon {
  margin-right: 13px; }

@media screen and ( max-width:  $tablet) {
  .header {
    justify-content: space-between; }

  .wrapper {
    width: auto;
    flex-direction: column;
    align-items: flex-end; }

  .location {
    margin-top: 9px; }

  .icon {
    margin-right: 7.5px; } }
